import { SET_CONTACT_DATA, SET_SINGLE_CONTACT } from "../types/contactTypes";

import { useMutation } from "react-query";
import { toast } from "react-toastify";

import useGetSingleContact from "../../pages/contact/contactHooks/useGetSingleContact";

import { createAddress } from "./leadActions";
import errorHandle from "../../utils/errorHandle";
import { tenantAPI } from "../../config/api";

export const setContactData = (data) => (dispach) => {
  dispach({ type: SET_CONTACT_DATA, payload: data });
};

export const setSingleContact = (data) => (dispach) => {
  dispach({ type: SET_SINGLE_CONTACT, payload: data });
};

export const useConvetToContactUpdate = () => {
  const mutation = useMutation(
    async (data) =>
      await tenantAPI.post(`/contacts/`, {
        lead: data.uuid,
      }),
    {
      onSuccess: (data, variables) => {
        //   refetch();
        toast.success(data.data.Success);
      },
      onError: (error) => {
        errorHandle(error);
      },
    }
  );

  return { mutate: mutation.mutate };
};

export const useUpdateContact = (id) => {
  const { refetch } = useGetSingleContact(id, { enabled: true });

  const updateContact = async (data) => {
    createAddress(data);
    return await tenantAPI.patch(`/contacts/`, data);
  };

  const mutation = useMutation(updateContact, {
    onSuccess: (data, variables) => {
      refetch();
      toast.success(data.data.Success);
    },
    onError: (error) => {
      if (error.response) {
        errorHandle(error);
      }
    },
  });

  return { mutate: mutation.mutate };
};

export const useSendEmail = () => {
  const mutation = useMutation(
    async (data) => await tenantAPI.post(`/tportal/email/`, data),
    {
      onSuccess: (data, variables) => {
        toast.success(data.data.msg);
      },
      onError: (error) => {
        errorHandle(error);
      },
    }
  );

  return { mutate: mutation.mutate };
};
