import React from "react";

import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import { Grid, MenuItem } from "@mui/material";

import { PlayForWork } from "@mui/icons-material";

import { Formik, Form } from "formik";

import { useCreateNewLead } from "../../../redux/actions/leadActions";

import useCreateLead from "./useCreateLead";

import PersonalInfo from "./CreateComponents/PersonalInfo";
import CompanyInfo from "./CreateComponents/CompanyInfo";
import LeadInfo from "./CreateComponents/LeadInfo";
import SocialInfo from "./CreateComponents/SocialInfo";
import AdditionalInfo from "./CreateComponents/AdditionalInfo";
import AddressSectionWrapper from "./CreateComponents/AddressSectionWrapper";

import { useSelector } from "react-redux";

// import CountrySelect from '../../../components/formsUI/CountriesSelect';

const CreateNewLead = ({ handleDropDownClose, hideButton, matches }) => {
  const defaultState = {
    first_name: "",
    middle_name: "",
    last_name: "",
    primary_number: "+91",
    secondary_number: "+91",
    work_number: "+91",

    company_name: "",
    gst_number: "",
    title: "",
    primary_email: "",
    secondary_email: "",
    website: "",
    source: "Self",
    industry: "",
    fax_number: "",
    tags: "",

    status: "New",
    owner: "",

    skype: "",
    facebook: "",
    twitter: "",
    linkedin: "",
    whatsapp: "",

    address_line1: "",
    city1: "",
    state1: "",
    nationality1: "",
    pin_code1: "",

    address_line2: "",
    city2: "",
    state2: "",
    nationality2: "",
    pin_code2: "",

    address_line3: "",
    city3: "",
    state3: "",
    nationality3: "",
    pin_code3: "",

    description: "",
  };

  const { validation, handleClickOpen, handleClose } =
    useCreateLead(handleDropDownClose);

  const { mutate } = useCreateNewLead(handleDropDownClose);

  const { leadDailog } = useSelector((state) => state.layoutStore);

  const descriptionElementRef = React.useRef(null);
  React.useEffect(() => {
    if (leadDailog) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
  }, [leadDailog]);

  return (
    <>
      {!hideButton && (
        <MenuItem
          onClick={handleClickOpen}
          sx={
            !matches
              ? { cursor: "pointer" }
              : { cursor: "pointer", fontSize: 14 }
          }
        >
          <PlayForWork
            color='secondary.dark'
            className='me-2'
            sx={
              !matches
                ? { cursor: "pointer" }
                : { cursor: "pointer", fontSize: 17 }
            }
          />
          Lead
        </MenuItem>
      )}
      <Dialog
        fullWidth
        maxWidth='md'
        scroll='paper'
        open={leadDailog}
        onClose={handleClose}
        aria-labelledby='scroll-Dialog-title'
        aria-describedby='scroll-Dialog-content'
      >
        <Formik
          initialValues={{
            ...defaultState,
          }}
          validationSchema={validation}
          onSubmit={async (values, { resetForm }) => {
            mutate(values, { onSuccess: () => resetForm() });
          }}
        >
          <Form>
            <DialogTitle
              sx={{ background: "#ededed" }}
              className='border-bottom border-dark'
              id='scroll-Dialog-title'
            >
              New Lead
            </DialogTitle>
            <DialogContent className='mt-3'>
              <Grid container>
                <Grid container spacing={2}>
                  <PersonalInfo />
                  <CompanyInfo />
                  <LeadInfo />
                  <SocialInfo />
                  <AddressSectionWrapper />
                  <AdditionalInfo />
                </Grid>
              </Grid>
            </DialogContent>
            <DialogActions
              sx={{
                background: "#ededed",
              }}
              className='border-top border-dark'
            >
              <Grid
                container
                direction='row'
                justifyContent='flex-end'
                alignItems='center'
              >
                <Grid
                  item
                  xs={5}
                  sm={3}
                  md={2}
                  lg={2}
                  className='d-flex justify-content-around aligin-items-center'
                >
                  <Button
                    size='small'
                    onClick={handleClose}
                    type='submit'
                    variant='contained'
                    color='error'
                    className='text-capitalize'
                  >
                    Close
                  </Button>
                  <Button
                    variant='containd'
                    type='submit'
                    sx={{
                      background: "#C4C4C4",
                      padding: "4px 10px !important",
                    }}
                    className='text-capitalize'
                  >
                    Save
                  </Button>
                </Grid>
              </Grid>
            </DialogActions>
          </Form>
        </Formik>
      </Dialog>
    </>
  );
};

export default CreateNewLead;
