import React from "react";

import { useField, useFormikContext } from "formik";

import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/material.css";

const MobileNumberInput = ({ name, label, color, border, ...otherProps }) => {
  const [field, mata] = useField(name);

  const data = useFormikContext();

  const configTextfield = {
    ...field,
    ...otherProps,
  };

  const onChange = (text) => {
    data.setFieldValue(name, "+" + text);
  };

  if (mata && mata.touched && mata.error) {
    configTextfield.error = true;
    configTextfield.helperText = mata.error;
  }

  return (
    <>
      <PhoneInput
        containerStyle={{
          border: configTextfield.error
            ? "1px solid #d32f2f"
            : "1px solid #CACACA",
        }}
        inputProps={{ ...configTextfield }}
        inputStyle={{
          "width": "100%",
          "background": "none",
          "borderRadius": border ? border : "0",
          "padding": "8.5px 50px",
          "&:focus": {
            borderColor: "#576a77",
          },
        }}
        defaultErrorMessage={configTextfield.helperText}
        value={configTextfield.value}
        onChange={(text) => onChange(text)}
        specialLabel={
          <span
            className='d-block px-1 mb-1'
            style={{
              color: configTextfield.error ? "#d32f2f" : "",
              background: color,
            }}
          >
            {label}
          </span>
        }
        enableSearch
      />
      <span
        style={{
          color: configTextfield.error ? "#d32f2f" : "",
          fontSize: "12px",
        }}
      >
        {configTextfield.helperText ? configTextfield.helperText : ``}
      </span>
    </>
  );
};

export default MobileNumberInput;
