import { SET_IS_COMPANY } from "../types/authTypes";

export const authReducer = (
  state = {
    pageNo: 1,
    userid: null,
    password: null,
  },
  action
) => {
  switch (action.type) {
    case SET_IS_COMPANY:
      return {
        ...state,
        pageNo: action.payload.pageNo,
        userid: action.payload.uuid ? action.payload.uuid : state.uuid,
        password: action.payload.password
          ? action.payload.password
          : state.password,
      };

    default:
      return { ...state };
  }
};
