import { SET_IS_COMPANY } from "../types/authTypes";
import { useState } from "react";
import { useMutation } from "react-query";
import { useDispatch } from "react-redux";
import errorHandle from "../../utils/errorHandle";
import { rootAPI, tenantAPI } from "../../config/api";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import setAuthToken from "../../utils/setAuthToken";

function setIsCompany(data) {
  return (dispatch) => {
    dispatch({ type: SET_IS_COMPANY, payload: data });
  };
}

//user register func

export const useUserRegister = () => {
  const dispatch = useDispatch();

  const mutation = useMutation(
    async (data) => await rootAPI.post(`/tenants/user/`, data),
    {
      onSuccess: (data, variables) => {
        dispatch(
          setIsCompany({
            uuid: data.data.id,
            password: variables.password,
            pageNo: 2,
          })
        );
        toast.success("User registered successfully.");
      },
      onError: (error) => {
        errorHandle(error);
      },
    }
  );

  return { mutate: mutation.mutate };
};
// comapny register func

export const useCompanyRegister = () => {
  const dispatch = useDispatch();

  const mutation = useMutation(
    async (data) => {
      return await rootAPI.post(`/tenants/tenant/`, data);
    },
    {
      onSuccess: (data) => {
        dispatch(setIsCompany({ pageNo: 3 }));
        toast.success(data.data.msg);
      },
      onError: (error) => {
        errorHandle(error);
      },
    }
  );

  return { mutate: mutation.mutate };
};

// user login page
export const useUserLogin = () => {
  const navigate = useNavigate();

  const mutation = useMutation(
    async (data) => await tenantAPI.post(`/accounts/login/`, data),
    {
      onSuccess: (data) => {
        localStorage.setItem("token", data.data.token);
        setAuthToken(data.data.token);
        return navigate("/");
      },
      onError: (error) => {
        errorHandle(error);
      },
    }
  );

  return { mutate: mutation.mutate };
};

