const Lead = () => {
  const { isLoading, createColumnSchema, data } = useHomeLead();
  const matches = useMediaQuery("(max-width:600px)");

  // const { leads } = useSelector((state) => state.leadStore);

  useEffect(() => {
    document.title = "All Leads | CRM";
  }, []);
  const { handleClickOpen } = useCreateLeadFunctional(() => {});
  useHotkeys("ctrl+alt+l", (e) => {
    e.preventDefault();
    handleClickOpen();
  });
  return (
    <PrivateRoute>
      <Navbar />
      <SideNavbar />
      <Header name="Lead" />
      <CreateNewLead hideButton={true} matches={matches} />
      <Container>
        <Table
          mt={"30px"}
          data={data ? data : row}
          loading={isLoading}
          createColumnSchema={createColumnSchema}
        />
        {!matches && <LeadFooter data={data ? data : row} />}
      </Container>
    </PrivateRoute>
  );
};

