import { useQuery } from "react-query";
import { tenantAPI } from "../../../config/api";

const useContactData = ({ options }) => {
  return useQuery("contacts", async () => await tenantAPI.get(`/contacts/`), {
    ...options,
    select: (data) => {
      return data.data.map((r) => ({ ...r, id: r.uuid }));
    },
  });
};

export default useContactData;
