import "./index.css";

import { ThemeProvider } from "@mui/material";

import { Routes, Route, Navigate } from "react-router-dom";

import theme from "./config/theme";

import Navbar from "./components/Layout/navbar/Navbar";
import SideNavbar from "./components/Layout/SideNavbar";

import Contact from "./pages/contact/indexContact";
import Lead from "./pages/lead/indexLead";
import Register from "./pages/user/register";
import Deal from "./pages/deal/indexDeal";
import CompanyAuth from "./pages/user/companyAuth";
import PrivateRoute from "./utils/PrivateRoute";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const App = () => {
  return (
    <ThemeProvider theme={theme}>
      <Routes>
        <Route
          path="/"
          element={
            <PrivateRoute>
              <Navbar />
              <SideNavbar />
              <h1 style={{ margin: "70px  70px" }}>Home page</h1>
            </PrivateRoute>
          }
        ></Route>
        <Route path="/leads" element={<Lead />} />
        <Route path="/contacts/*" element={<Contact />} />
        <Route path="/deals/*" element={<Deal />} />
        <Route path="/my-crm/*" element={<CompanyAuth />} />
        <Route path="/register" element={<Register />}></Route>
      </Routes>
      <ToastContainer
        position="bottom-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </ThemeProvider>
  );
};

export default App;
