import React from "react";
import moment from "moment";

import { Grid, Box, Typography } from "@mui/material";

import { useSelector } from "react-redux";

const ContactFooter = () => {
  const { contact } = useSelector((state) => state.contactStore);
  const { created_on } = contact;
  return (
    <Grid
      container
      sx={{
        padding: "5px 0",
        marginBottom: "20px",
      }}
      display='grid'
      direction='column'
      alignItems='center'
    >
      <Grid
        item
        xs={12}
        alignItems='center'
        sx={{ paddingLeft: "10px", marginTop: "5px" }}
      >
        <Box
          className='d-flex align-items-end'
          sx={{
            maxWidth: "90%",
          }}
        >
          <Typography
            variant='p'
            className='second'
            fontSize='14px'
            sx={{
              marginRight: "5px",
              fontWeight: "medium",
              inlineSize: "100%",
              overflowWrap: "break-word",
            }}
          >
            {moment(created_on).format("DD/MM/YYYY")} &nbsp;
            <span
              style={{
                fontSize: "12px",
                marginBottom: "1px",
                overflowWrap: "normal",
              }}
            >
              added on
            </span>
          </Typography>
        </Box>
      </Grid>
    </Grid>
  );
};

export default ContactFooter;
