import useContactData from "../contactHooks/useContactData";
import { Link } from "react-router-dom";
import { Person } from "@mui/icons-material";

const useContactTable = () => {
  const { isLoading, data } = useContactData({});

  const createColumnSchema = (data) => {
    return (
      data &&
      Object.keys(data)
        .map((key, pos) => ({
          field: key,
          headerName: key.replace("_", " "),
          minWidth: 150,
          hide: pos > 10 ? true : false,
          headerClassName: "text-capitalize",
          cellClassName: "p-0",
          flex: 1,
          renderCell: (params) => {
            return params.field === "first_name" ? (
              <Link
                to={`/contacts/${params.id}`}
                className='ps-3 primary-main text-truncate second'
                style={{
                  width: "100%",
                  height: "100%",
                }}
              >
                <Person className='mb-1 me-2' />
                {params.value}
              </Link>
            ) : (
              <Link
                to={`/contacts/${params.id}`}
                className='ps-3 primary-main text-truncate second'
                style={{
                  width: "100%",
                  height: "100%",
                  textDecoration: "none",
                }}
              >
                {" "}
                {params.value}
              </Link>
            );
          },
        }))
        .slice(1)
    );
  };

  return { isLoading, createColumnSchema, data };
};

export default useContactTable;
