import { useDispatch } from "react-redux";

import * as Yup from "yup";
import {
  openLeadDailog,
  closeLeadDailog,
} from "../../../redux/actions/LayoutAction";

const useCreateLeadFunctional = () => {
  const dispatch = useDispatch();

  const validation = Yup.object().shape({
    first_name: Yup.string().required("This field is required"),
    middle_name: Yup.string().required("This field is required"),
    last_name: Yup.string().required("This field is required"),
    primary_number: Yup.string().required("This field is required"),
    secondary_number: Yup.string().nullable(),
    work_number: Yup.string().nullable(),

    company_name: Yup.string().required("This field is required"),
    gst_number: Yup.string().required("This field is required"),
    primary_email: Yup.string()
      .email("Please enter a valid email.")
      .required("This field is required"),
    secondary_email: Yup.string().email("Please enter a valid email."),
    website: Yup.string().matches(
      /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
      "Enter correct url!"
    ),
    fax_number: Yup.string().nullable(),
    title: Yup.string().nullable(),
    source: Yup.string().required("This field is required"),
    industry: Yup.string().required("This field is required"),
    tags: Yup.string().nullable(),

    status: Yup.string().required("This field is required"),
    owner: Yup.string().required("This field is required"),

    skype: Yup.string()
      .matches(
        /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
        "Enter correct url!"
      )
      .nullable(),
    facebook: Yup.string()
      .matches(
        /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
        "Enter correct url!"
      )
      .nullable(),
    twitter: Yup.string()
      .matches(
        /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
        "Enter correct url!"
      )
      .nullable(),
    linkedin: Yup.string()
      .matches(
        /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
        "Enter correct url!"
      )
      .nullable(),
    whatsapp: Yup.string().nullable(),

    address_line1: Yup.string().nullable(),
    city1: Yup.string().nullable(),
    state1: Yup.string().nullable(),
    nationality1: Yup.string().nullable(),
    pin_code1: Yup.string().nullable(),

    address_line2: Yup.string().nullable(),
    city2: Yup.string().nullable(),
    state2: Yup.string().nullable(),
    nationality2: Yup.string().nullable(),
    pin_code2: Yup.string().nullable(),

    address_line3: Yup.string().nullable(),
    city3: Yup.string().nullable(),
    state3: Yup.string().nullable(),
    nationality3: Yup.string().nullable(),
    pin_code3: Yup.string().nullable(),

    description: Yup.string().nullable(),
  });

  const handleClickOpen = () => {
    dispatch(openLeadDailog());
  };

  const handleClose = () => {
    dispatch(closeLeadDailog());
  };

  return {
    validation,
    handleClickOpen,
    handleClose,
  };
};

export default useCreateLeadFunctional;
