import React, { useEffect, useRef, useState } from "react";

import Menu from "@mui/material/Menu";
import { Box } from "@mui/material";
import { useSelector } from "react-redux";

const DropDown = ({ button, children, ...otherMenuProps }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const ref = useRef();
  const { leadDailog } = useSelector((state) => state.layoutStore);
  useEffect(() => {
    if (!leadDailog) {
      setAnchorEl(null);
    }
  }, [leadDailog]);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <React.Fragment>
      <Box onClick={handleClick} ref={ref}>
        {button}
      </Box>

      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        {...otherMenuProps}
      >
        {children}
      </Menu>
    </React.Fragment>
  );
};

export default DropDown;
