import React from "react";

import {
  Box,
  Typography,
  Grid,
  Button,
  Stack,
  useMediaQuery,
} from "@mui/material";

import LeadDrower from "../../pages/lead/leadDrower";

import { useUpdateContact } from "../../redux/actions/contactAction";

import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";

const ContactHeader = () => {
  const matches = useMediaQuery("(max-width:600px)");
  const { contact } = useSelector((state) => state.contactStore);

  const { first_name, last_name, company_name, title } = contact;
  const params = useParams();
  const { mutate } = useUpdateContact(params.uuid);
  return (
    <Grid
      container
      display='row'
      className='bg-fifth'
      sx={{
        borderBottom: "2px solid #ced4da",
        paddingTop: "3.5rem",
        paddingBottom: "10px",
        paddingLeft: !matches ? "74px" : "70px",
      }}
    >
      <Box sx={{ width: "100%" }} className='d-flex align-items-center ps-1'>
        <Grid container>
          <Grid item xs={6} sm={4} md={2}>
            <Stack>
              <Typography variant='p' className='second mt-2' fontSize='12px'>
                Contacts
              </Typography>
              <Typography
                variant='p'
                className='second'
                fontSize='20px'
                sx={{ fontWeight: "medium" }}
              >
                {first_name + " " + last_name}
              </Typography>
              <Typography variant='p' className='second' fontSize='12px'>
                {title + "at" + company_name}
              </Typography>
            </Stack>
          </Grid>
          <Grid item xs={6} sm={8} md={10} className='align-self-center'>
            <LeadDrower
              name={
                <Button
                  variant='outlined'
                  sx={{
                    "color": "#576a77",
                    "border": "1px solid #576a77",
                    "padding": "3px 5px",
                    "fontSize": "12px",
                    "&:hover": {
                      border: "1px solid #576a77",
                    },
                  }}
                >
                  {" "}
                  Edit
                </Button>
              }
              heading='Contact Card'
              data={contact}
              mutate={mutate}
            />
          </Grid>
          {/* <Grid item xs={12}>
       
          </Grid> */}
        </Grid>
      </Box>
    </Grid>
  );
};

export default ContactHeader;
