import React, { useState } from "react";

import {
  Grid,
  InputAdornment,
  Stack,
  Box,
  Typography,
  useMediaQuery,
} from "@mui/material";

import { makeStyles } from "@mui/styles";

import { Form, Formik } from "formik";

import SubmitButton from "../../../formsUI/SubmitButton";
import TextEditor from "../../../Layout/Editor/RichTextEditor";
import AutoComplete from "../../../formsUI/AutoComplete";
import InputField from "../../../formsUI/InputField";
import { useSendEmail } from "../../../../redux/actions/contactAction";
import * as Yup from "yup";
const useStyle = makeStyles({
  textField: {
    "background": "#f8f9fa",
    "& .MuiFilledInput-root": {
      background: "none",
    },
    "& .MuiInputAdornment-root": {
      width: 80,
    },
    "& .MuiFilledInput-root:after": {
      display: "none",
    },
  },
});

const SendEmailBox = () => {
  const classes = useStyle();

  const [isCc, setIsCc] = useState(false);
  const [isBcc, setIsBcc] = useState(false);

  const matches = useMediaQuery("(max-width:600px)");

  const defaultState = {
    to: [],
    cc: [],
    bcc: [],
    subject: "",
    body: "",
  };

  const { mutate } = useSendEmail();

  const validation = Yup.object().shape({
    to: Yup.array()
      .min(1, "This field is required")
      .required("This field is required"),
    cc: Yup.array(),
    bcc: Yup.array(),
    subject: Yup.string().required("This field is required"),
    body: Yup.string().required("This field is required"),
  });

  return (
    <Formik
      initialValues={{ ...defaultState }}
      validationSchema={validation}
      onSubmit={async (values, { resetForm }) => {
        const data = {
          ...values,
          to: values.to ? values.to.map((item) => item.value) : [],
          cc: values.cc ? values.cc.map((item) => item.value) : [],
          bcc: values.bcc ? values.bcc.map((item) => item.value) : [],
        };
        mutate(data, { onSuccess: () => resetForm() });
      }}
    >
      <Form style={{ width: "100%" }}>
        <Grid container direction='row' className='bg-forth p-3'>
          <Grid item xs={12} sm={9} md={9} className='ps-3 bg-fifth'>
            <Stack>
              {matches && (
                <Grid item xs={12} className='bg-fifth'>
                  <Box
                    className='d-flex align-items-center justify-content-start'
                    sx={{ height: 39 }}
                  >
                    <span
                      className='px-2 py-1 bg-forth rounded me-2'
                      style={{ cursor: "pointer", fontSize: "12px" }}
                      onClick={() => setIsCc(!isCc)}
                    >
                      Cc
                    </span>
                    <span
                      className='px-2 py-1 bg-forth rounded me-2'
                      onClick={() => setIsBcc(!isBcc)}
                      style={{ cursor: "pointer", fontSize: "12px" }}
                    >
                      Bcc
                    </span>
                    <Typography
                      varient='p'
                      className='text-end align-middle me-2 py-1 px-2'
                      sx={{
                        // padding: '8px 0',
                        cursor: "pointer",
                        fontSize: "12px",
                      }}
                    >
                      Templet
                    </Typography>
                  </Box>
                </Grid>
              )}
              <AutoComplete
                name='to'
                options={[
                  {
                    label: "Faizer Shaikh",
                    value: "faizer.shaikh@fladdra.com",
                  },
                  { label: "Sajid Shaikh", value: "sajid.shaikh@fladdra.com" },
                  {
                    label: "Shoaib Shaikh",
                    value: "shoaib.shaikh@fladdra.com",
                  },
                  {
                    label: "Taabish ansari",
                    value: "tabish.ansari@fladdra.com",
                  },
                ]}
                startAdornment={
                  <InputAdornment position='start'>To</InputAdornment>
                }
                multiple={true}
              />
              {isCc && (
                <AutoComplete
                  name='cc'
                  options={[
                    {
                      label: "Faizer Shaikh",
                      value: "faizer.shaikh@fladdra.com",
                    },
                    {
                      label: "Sajid Shaikh",
                      value: "sajid.shaikh@fladdra.com",
                    },
                    {
                      label: "Shoaib Shaikh",
                      value: "shoaib.shaikh@fladdra.com",
                    },
                    {
                      label: "Taabish ansari",
                      value: "tabish.ansari@fladdra.com",
                    },
                    {
                      label: "Yasin ansari",
                      value: "yasin.ansari@fladdra.com",
                    },
                    {
                      label: "Amaan Khan",
                      value: "amaan.khan@fladdra.com",
                    },
                  ]}
                  startAdornment={
                    <InputAdornment position='start'>Cc</InputAdornment>
                  }
                  multiple={true}
                />
              )}
              {isBcc && (
                <AutoComplete
                  name='bcc'
                  options={[
                    {
                      label: "Faizer Shaikh",
                      value: "faizer.shaikh@fladdra.com",
                    },
                    {
                      label: "Sajid Shaikh",
                      value: "sajid.shaikh@fladdra.com",
                    },
                    {
                      label: "Shoaib Shaikh",
                      value: "shoaib.shaikh@fladdra.com",
                    },
                    {
                      label: "Taabish ansari",
                      value: "tabish.ansari@fladdra.com",
                    },
                    {
                      label: "Yasin ansari",
                      value: "yasin.ansari@fladdra.com",
                    },
                    {
                      label: "Amaan Khan",
                      value: "amaan.khan@fladdra.com",
                    },
                  ]}
                  startAdornment={
                    <InputAdornment position='start'>Bcc</InputAdornment>
                  }
                  multiple={true}
                />
              )}
              <InputField
                hiddenLabel
                id='filled-hidden-label-small'
                fullWidth
                variant='filled'
                className={classes.textField}
                size='small'
                name='subject'
                InputProps={{
                  startAdornment: (
                    <InputAdornment position='start'>Subject</InputAdornment>
                  ),
                }}
              />
              <TextEditor name='body' />
            </Stack>
          </Grid>
          {!matches && (
            <Grid item xs={12} sm={3} md={3} className='bg-fifth px-3'>
              <Stack>
                <Box
                  className='d-flex align-items-center justify-content-end'
                  sx={{ height: 39 }}
                >
                  <span
                    className='px-2 py-1 bg-forth rounded me-2'
                    style={{ cursor: "pointer" }}
                    onClick={() => setIsCc(!isCc)}
                  >
                    Cc
                  </span>
                  <span
                    className='px-2 py-1 bg-forth rounded '
                    onClick={() => setIsBcc(!isBcc)}
                    style={{ cursor: "pointer" }}
                  >
                    Bcc
                  </span>
                </Box>
                <Typography
                  varient='p'
                  className='text-end align-middle'
                  sx={{ height: 39, padding: "8px 0", cursor: "pointer" }}
                >
                  Templet
                </Typography>
              </Stack>
            </Grid>
          )}
          <Grid item xs={12}>
            <SubmitButton
              variant='containd'
              type='submit'
              className='text-capitalize bg-first fifth px-3 py-1 ms-auto d-block mt-2'
            >
              Send
            </SubmitButton>
          </Grid>
        </Grid>
      </Form>
    </Formik>
  );
};

export default SendEmailBox;
