import { SET_CONTACT_DATA, SET_SINGLE_CONTACT } from '../types/contactTypes';

export const contactReducer = (
  state = {
    contact: {
      address: '-',
      billing_address: '-',
      company_name: '-',
      created_on: '-',
      description: '-',
      facebook: '-',
      fax_number: '-',
      first_name: '-',
      gst_number: '-',
      industry: '-',
      last_name: '-',
      linkedin: '-',
      middle_name: '-',
      owner: '-',
      primary_email: '-',
      primary_number: '-',
      secondary_email: '-',
      secondary_number: '-',
      shipping_address: '-',
      skype: '-',
      source: '-',
      status: '-',
      tags: '-',
      title: '-',
      twitter: '-',
      updated_on: '-',
      uuid: '-',
      website: '-',
      work_number: '-',
    },
  },
  action
) => {
  switch (action.type) {
    case SET_CONTACT_DATA:
      return {
        ...state,
        contacts: action.payload,
      };
    case SET_SINGLE_CONTACT:
      return {
        ...state,
        contact: action.payload[0],
      };

    default:
      return { ...state };
  }
};
