import React, { useEffect, useState } from "react";

import { DataGridPro } from "@mui/x-data-grid-pro";

import GridOverlayComp from "./GridOverlay";
import CustomLoadingOverlay from "./LoadingOverlay";
import CustomToolbar from "./CustomToolbar";
import { makeStyles } from "@mui/styles";

import { PlayForWork } from "@mui/icons-material";

const columns = [
  {
    field: "",
    headerName: "Lead",
    width: 108,
    disableExport: true,
    renderCell: () => <PlayForWork />,
    sortable: false,
  },
  {
    field: "full_name",
    headerName: "Full name",
    width: 150,
    valueGetter: (params) =>
      `${params.getValue(params.id, "first_name") || ""} ${
        params.getValue(params.id, "last_name") || ""
      }`,
  },
  {
    field: "company_name",
    headerName: "Company",
    width: 200,
  },
  {
    field: "status",
    headerName: "Status",
    width: 150,
  },
  {
    field: "owner",
    headerName: "Lead Owner",
    width: 200,
  },
  {
    field: "source",
    headerName: "Lead Source",
    width: 160,
  },
  {
    field: "city",
    headerName: "City",
    width: 120,
  },
  {
    field: "mobile_number",
    headerName: "Phone Number",
    width: 180,
  },
  {
    field: "title",
    headerName: "Title",
    width: 120,
  },
];

const escapeRegExp = (value) => {
  return value.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, "\\$&");
};

const useStyles = makeStyles((theme) => ({
  root: {
    "borderRadius": 5,
    "& .MuiTablePagination-root": {
      marginBottom: "5rem",
    },
    "& .MuiTablePagination-selectLabel,& .MuiTablePagination-displayedRows,": {
      marginBottom: 0,
      color: "#495057",
    },
    "& .MuiTablePagination-toolbar": {
      dispaly: "flex",
      aligItems: "center",
      justifyContent: "space-between",
    },
    "& .MuiCheckbox-root.Mui-checked": {
      color: "#000",
    },
    "& .MuiDataGrid-columnHeaders": {
      "backgroundColor": "#ced4da",
      "border": "none !important",
      "color": "#4C6371",
      "& .MuiDataGrid-columnSeparator": {
        color: "#4C6371",
      },
      [theme.breakpoints.down("sm")]: {
        minHeigth: "45 !important",
        maxHeigth: "45 !important",
        lineHeight: "45 !important",
      },
    },
    "& .MuiButton-root": {
      color: "#60859d",
      border: "2px solid #60859d",
      padding: "5px 10px",
    },
    "& .MuiDataGrid-row:nth-child(even)": {
      backgroundColor: "#E9ECEF",
      [theme.breakpoints.down("sm")]: {
        minHeigth: "45 !important",
        maxHeigth: "45 !important",
        lineHeight: "45 !important",
      },
    },
    "& .MuiDataGrid-row:nth-child(odd)": {
      backgroundColor: "#F8F9FA",
      [theme.breakpoints.down("sm")]: {
        minHeigth: "45 !important",
        maxHeigth: "45 !important",
        lineHeight: "45 !important",
      },
    },
  },
}));

const Table = ({ data, loading, createColumnSchema }) => {
  const [pageSize, setPageSize] = useState(10);
  const [searchText, setSearchText] = useState("");
  const [rows, setRows] = useState(data);
  const requestSearch = (searchValue) => {
    setSearchText(searchValue);
    const searchRegex = new RegExp(escapeRegExp(searchValue), "gi");
    const filteredRows = data.filter((row) =>
      Object.keys(row).some((field) =>
        searchRegex.test(row[field] ? row[field].toString() : "")
      )
    );
    setRows(filteredRows);
  };

  const clasess = useStyles();

  useEffect(() => {
    if (data) {
      setRows(data);
    }
    //eslint-disable-next-line
  }, [data]);
  return (
    <div
      style={{
        height: 400,
        width: "100%",
      }}
      className='mt-5 table-wrapper'
    >
      <DataGridPro
        className={clasess.root}
        onPageSizeChange={(newPageSize) => setPageSize(newPageSize)}
        rowsPerPageOptions={[10, 15, 20, 25].filter(
          (size) => data && data.length >= size
        )}
        components={{
          LoadingOverlay: CustomLoadingOverlay,
          NoRowsOverlay: GridOverlayComp,
          Toolbar: CustomToolbar,
        }}
        componentsProps={{
          toolbar: {
            value: searchText,
            onChange: (event) => requestSearch(event.target.value),
            clearSearch: () => requestSearch(""),
          },
        }}
        columns={data[0] ? createColumnSchema(data[0]) : columns}
        rows={rows}
        pageSize={pageSize}
        checkboxSelection
        disableSelectionOnClick
        loading={loading}
        rowHeight={50}
        pagination
        hideFooter={rows.length === 0 ? true : false}
        autoHeight={rows.length !== 0 ? true : false}
      />
    </div>
  );
};

export default Table;
