import React from "react";

import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import { MenuItem, Grid, Divider, DialogActions } from "@mui/material";

import { PublishedWithChanges } from "@mui/icons-material";

import InputField from "../../../components/formsUI/InputField";

import { Form, Formik } from "formik";

import useConvertToContact from "./useConvertToContact";

import { useConvetToContactUpdate } from "../../../redux/actions/contactAction";
import { useLeadUpdate } from "../../../redux/actions/leadActions";

const ConvertToContact = ({ handleClose, data }) => {
  const { open, defaultState, validation, handleClickOpen, handleCtoCClose } =
    useConvertToContact(handleClose, data);

  const { mutate } = useConvetToContactUpdate();
  const { mutate: updateLead } = useLeadUpdate();

  return (
    <>
      <MenuItem onClick={handleClickOpen}>
        <PublishedWithChanges fontSize='18' className='me-2' />
        Convert
      </MenuItem>
      <Dialog
        open={open}
        onClose={handleCtoCClose}
        aria-labelledby='alert-Dialog-title'
        aria-describedby='alert-Dialog-description'
      >
        <Formik
          initialValues={{
            ...defaultState,
          }}
          validationSchema={validation}
          onSubmit={async (values) => {
            updateLead(values);
            mutate(values, { onSuccess: () => handleCtoCClose() });
          }}
        >
          <Form>
            <DialogTitle id='alert-Dialog-title' className='border-bottom'>
              {"Are you sure do you want to covert this lead?"}
            </DialogTitle>
            <DialogContent>
              <DialogContentText id='alert-Dialog-description' className='mt-1'>
                The conversion of this lead will create contact of a person or
                an company.
                <br />
                <br />
                All fields and information will be transferred to contact. You
                should only convert if it's a qualified lead.
              </DialogContentText>
              <Grid container spacing={2}>
                <Grid item xs={12} mt={2}>
                  <Divider>Owner Info</Divider>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputField
                    name='owner'
                    label='Owner *'
                    color='secondary'
                    size='small'
                  />
                </Grid>
                <Grid item xs={12}>
                  <Divider>Personal Info</Divider>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputField
                    name='first_name'
                    label='First Name *'
                    color='secondary'
                    size='small'
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputField
                    name='middle_name'
                    label='Middle Name *'
                    color='secondary'
                    size='small'
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputField
                    name='last_name'
                    label='Last Name *'
                    color='secondary'
                    size='small'
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputField
                    name='primary_number'
                    label='Primary Number *'
                    color='secondary'
                    size='small'
                  />
                </Grid>
                <Grid item xs={12}>
                  <Divider>Company Info</Divider>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputField
                    name='company_name'
                    label='Company Name *'
                    color='secondary'
                    size='small'
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputField
                    name='gst_number'
                    label='GST Number *'
                    color='secondary'
                    size='small'
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputField
                    name='primary_email'
                    label='Primary Email Address *'
                    color='secondary'
                    size='small'
                  />
                </Grid>
              </Grid>
            </DialogContent>

            <DialogActions className='border-top'>
              <Button
                onClick={handleCtoCClose}
                color='error'
                variant='outlined'
              >
                Cancle
              </Button>
              <Button
                // onClick={handleCtoCClose}
                type='submit'
                autoFocus
                variant='contained'
              >
                Convert
              </Button>
            </DialogActions>
          </Form>
        </Formik>
      </Dialog>
    </>
  );
};

export default ConvertToContact;
