import React from 'react';

import { Box, useMediaQuery } from '@mui/material';

const Container = ({ children, ...rest }) => {
  const matches = useMediaQuery('(max-width:600px)');
  return (
    <Box
      sx={{
        paddingLeft: !matches ? '74px' : '68px',
        paddingRight: '24px',
        width: '100%',
      }}
      {...rest}>
      {children}
    </Box>
  );
};

export default Container;
