import { SET_LEAD_DATA } from "../types/leadTypes";

import { useQueryClient, useMutation } from "react-query";
import { toast } from "react-toastify";

import { useDispatch } from "react-redux";
import { closeLeadDailog } from "./LayoutAction";

import useLeadData from "../../pages/lead/leadHooks/useLeadData";
import errorHandle from "../../utils/errorHandle";
import { tenantAPI } from "../../config/api";

export const createAddress = (data) => {
  data["address"] = [
    data.address_line1,
    data.city1,
    data.state1,
    data.nationality1,
    data.pin_code1,
  ]
    .filter((item) => item)
    .join("^");
  data["billing_address"] = [
    data.address_line2,
    data.city2,
    data.state2,
    data.nationality2,
    data.pin_code2,
  ]
    .filter((item) => item)
    .join("^");
  data["shipping_address"] = [
    data.address_line3,
    data.city3,
    data.state3,
    data.nationality3,
    data.pin_code3,
  ]
    .filter((item) => item)
    .join("^");
};

export const useCreateNewLead = () => {
  const dispatch = useDispatch();

  const { refetch } = useLeadData({ enabled: true });
  const createNewLead = async (data) => {
    createAddress(data);
    return await tenantAPI.post(`/leads/0/`, data);
  };

  const mutation = useMutation(createNewLead, {
    onSuccess: (data) => {
      toast.success(data.data.Success);
      dispatch(closeLeadDailog());
      refetch();
    },
    onError: (error) => {
      errorHandle(error);
    },
  });

  return { mutate: mutation.mutate };
};

export const setLeadData = (data) => (dispach) => {
  dispach({ type: SET_LEAD_DATA, payload: data });
};

export const useLeadUpdate = () => {
  const queryClient = useQueryClient();

  const updateLead = async (data) => {
    createAddress(data);
    return await tenantAPI.patch(`/leads/`, data);
  };

  const { refetch } = useLeadData({
    enabled: true,
  });
  const mutation = useMutation(updateLead, {
    onSuccess: (data, variables) => {
      queryClient.setQueryData(["leads", { id: variables.id }], data);
      refetch();
      toast.success(data.data.Success);
    },
    onError: (error) => {
      errorHandle(error);
    },
  });

  return { mutate: mutation.mutate };
};
