import { OPEN_LEAD_DAILOG, CLOSE_LEAD_DAILOG } from "../types/layoutTypes";

export const layoutReducer = (
  state = {
    leadDailog: false,
  },
  action
) => {
  switch (action.type) {
    case OPEN_LEAD_DAILOG:
      return {
        ...state,
        leadDailog: true,
      };
    case CLOSE_LEAD_DAILOG:
      return {
        ...state,
        leadDailog: false,
      };
    default:
      return { ...state };
  }
};
