import React from "react";

import { NavLink as Link, useNavigate } from "react-router-dom";

import { Box, Grid, Avatar, Tooltip } from "@mui/material";

import {
  Home,
  PlayForWork,
  Person,
  AttachMoney,
  Event,
  TaskAlt,
  ModeComment,
  Leaderboard,
  PermPhoneMsg,
  AccountTree,
  Settings,
} from "@mui/icons-material";

import img from "../../media/pp.jpeg";
import logo from "../../media/logo.png";

import { useHotkeys } from "react-hotkeys-hook";

const SideNavbar = () => {
  let navigate = useNavigate();

  useHotkeys("ctrl+h", (e) => {
    e.preventDefault();
    navigate("/", { replace: true });
  });
  useHotkeys("ctrl+l", (e) => {
    e.preventDefault();
    navigate("/leads", { replace: true });
  });
  useHotkeys("ctrl+c", (e) => {
    e.preventDefault();
    navigate("/contacts", { replace: true });
  });
  useHotkeys("ctrl+d", (e) => {
    e.preventDefault();
    navigate("/deals", { replace: true });
  });

  return (
    <Box sx={{ height: "100%", width: 50 }} className='sideNavbar bg-first'>
      <Grid
        container
        direction='column'
        alignItems='center'
        justifyContent='space-between'
      >
        <Grid item sm={1} sx={{ padding: "20% 0" }} className='fifth'>
          <Avatar alt='avatar' src={img} sx={{ width: 30, height: 30 }} />
        </Grid>
        <Tooltip title='Home' arrow placement='right'>
          <Link
            to='/'
            className='d-flex justify-content-center align-items-center fifth navbar-link'
            style={{ padding: "20% 0" }}
          >
            {" "}
            <Home />
          </Link>
        </Tooltip>
        <Tooltip title='Leads' arrow placement='right'>
          <Link
            to='/leads'
            className='d-flex justify-content-center align-items-center fifth navbar-link'
            style={{ padding: "20% 0" }}
          >
            <PlayForWork />
          </Link>
        </Tooltip>
        <Tooltip title='Contact' arrow placement='right'>
          <Link
            to='/contacts'
            className='d-flex justify-content-center align-items-center fifth navbar-link'
            style={{ padding: "20% 0" }}
          >
            <Person />
          </Link>
        </Tooltip>
        <Tooltip title='Deal' arrow placement='right'>
          <Link
            to='/deals'
            className='d-flex justify-content-center align-items-center fifth navbar-link'
            style={{ padding: "20% 0" }}
          >
            <AttachMoney />
          </Link>
        </Tooltip>
        <Grid item sm={1} sx={{ padding: "20% 0" }} className='fifth'>
          <Event />
        </Grid>
        <Grid item sm={1} sx={{ padding: "20% 0" }} className='fifth'>
          <TaskAlt />
        </Grid>
        <Grid item sm={1} sx={{ padding: "20% 0" }} className='fifth'>
          <ModeComment />
        </Grid>
        <Grid item sm={1} sx={{ padding: "20% 0" }} className='fifth'>
          <Leaderboard />
        </Grid>
        <Grid item sm={1} sx={{ padding: "20% 0" }} className='fifth'>
          <AccountTree />
        </Grid>
        <Grid item sm={1} sx={{ padding: "20% 0" }} className='fifth'>
          <PermPhoneMsg />
        </Grid>
        <Grid item sm={1} sx={{ padding: "20% 0" }} className='fifth'>
          <Settings />
        </Grid>
        <Grid item sx={{ position: "absolute", bottom: "12px" }}>
          <Avatar alt='avatar' src={logo} sx={{ width: 30, height: 30 }} />
        </Grid>
      </Grid>
    </Box>
  );
};

export default SideNavbar;
